nav{
    background-color: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 1.3rem;
    display: flex;
    gap:0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
 
   
}

nav a{
    background: transparent;
    padding: 0.35rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
   
}
nav a:hover{
    background: rgba(77, 141, 154, 0.4);
}
nav a.active{
    background: var(--color-primary);
    color: var(--color-white);
}