header{
    height: 100vh;
    padding-top: 1.2rem;
    overflow:hidden;
}
.header__container{
    text-align: center;
    height: 100%;
    position: relative;
   
}
/**********CTA************/
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap:1.2rem;
    justify-content: center;
    
    
}
/*****Header Socials*****/

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:0.8rem;
    position: absolute;
    left: 0;
    bottom:3rem;
    font-size: 25px;
}
.header__socials::after{
    content:"";
    width:2px;
    height:2rem;
    background: var(--color-primary);

}

.me{
    background: linear-gradient(var(--color-primary), transparent);
   
    width: 22rem;
    height:30rem;
    position:absolute;
    left:50%;
    transform: translateX(-50%);
    border-radius: 12rem 12rem 0 0;
    margin-top: 2rem;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
 
    

}
.scroll__down{
    position: absolute;
    right: -2.5rem;
    bottom:5rem;
    ;
    font-weight: 300;
    font-size: 1.8rem;
}
/****Tabels *****/
@media screen and (max-width:1024px){

    header{
        height:68vh;
    }
}

/****Mobile Phones*****/
@media screen and (max-width:600px){

    header{
        height: 100vh;
    }

    .scroll__down,.header__socials{
        display: none;

    }
}